<!--  -->
<template>
  <div class="m-product-category">
    <dl class="classic">
      <dt></dt>
      <dt></dt>
      <dd v-for="(item) in menuList" :key="item.type">
        <m-select :name="item.title" :list="item.subList" />
      </dd>
    </dl>
    <dl class="classic">
      <dt></dt>
      <dt></dt>
      <dd v-for="(item) in areaList" :key="item.type">
        <m-select :name="item.title" :list="item.subList" />
      </dd>
    </dl>
  </div>
</template>

<script>
import MSelect from "./select";
import api from "@/api/index.js";
export default {
  data() {
    return {
      menuList: [],
      areaList: [
        {
          title: "",
          subList: [
            {
              name: "",
              id: 120000
            },
            {
              name: "",
              id: 12222
            }
          ]
        },
        {
          title: "",
          subList: [
            {
              name: "",
              id: 123
            },
            {
              name: "",
              id: 11
            },
            {
              name: "",
              id: 222
            },
            {
              name: "/",
              id: 21
            },
            {
              name: "",
              id: 1
            },
            {
              name: "",
              id: 34
            }
          ]
        }
      ]
    };
  },
  created() {
    api.getClassify().then(res => {
      this.menuList = res.data.data;
    });
  },
  components: {
    MSelect
  }
};
</script>

<style lang="scss">
</style>


