import axios from 'axios'
axios.defaults.baseURL = 'http://api.duyiedu.com'
axios.interceptors.request.use(function (config) {
  // 
  config.params = {
    ...config.params,
    appkey: 'coderljp_1581060918858'
  }
  return config
}, function (error) {
// 
  return Promise.reject(error)
})

export default axios



